import { generateRandomId, cloneObject } from '../utils/helpers';

const todoActions = () => {
    const addTodo = () => prevState => {
        const newTodo = {
            id: generateRandomId(),
            title: 'new todo',
            isComplete: false,
            subTodos: []
        };

        return [...prevState, newTodo];
    };

    const addSubTodo = todoId => prevState => {
        const newSubTodo = {
            id: generateRandomId(),
            title: 'new  sub todo',
            isComplete: false
        };
        const clonedTodos = cloneObject(prevState);
        const todoIndex = clonedTodos.findIndex(todo => todo.id === todoId);

        clonedTodos[todoIndex].subTodos.push(newSubTodo);

        clonedTodos[todoIndex].isComplete = false;

        return clonedTodos;
    };

    const toggleTodo = todoId => prevState => {
        const clonedTodos = cloneObject(prevState);
        const todoIndex = clonedTodos.findIndex(todo => todo.id === todoId);

        clonedTodos[todoIndex].isComplete = !clonedTodos[todoIndex].isComplete;

        // toggle all subTodos to todo status as well
        clonedTodos[todoIndex].subTodos.forEach(
            subTodo => (subTodo.isComplete = clonedTodos[todoIndex].isComplete)
        );

        return clonedTodos;
    };

    const toggleSubTodo = (todoId, subTodoId) => prevState => {
        const clonedTodos = cloneObject(prevState);
        const todoIndex = clonedTodos.findIndex(todo => todo.id === todoId);
        const subTodos = clonedTodos[todoIndex].subTodos;
        const subTodoIndex = subTodos.findIndex(
            subTodo => subTodo.id === subTodoId
        );

        subTodos[subTodoIndex].isComplete = !subTodos[subTodoIndex].isComplete;

        // is subtodo is not complete then uncheck parent todo
        if (!subTodos[subTodoIndex].isComplete) {
            clonedTodos[todoIndex].isComplete = false;
        }

        return clonedTodos;
    };

    const deleteTodo = todoId => prevState => {
        const clonedTodos = cloneObject(prevState);
        const todoIndex = clonedTodos.findIndex(todo => todo.id === todoId);

        clonedTodos.splice(todoIndex, 1);

        return clonedTodos;
    };

    const deleteSubTodo = (todoId, subTodoId) => prevState => {
        const clonedTodos = cloneObject(prevState);
        const todoIndex = clonedTodos.findIndex(todo => todo.id === todoId);
        const subTodoIndex = clonedTodos[todoIndex].subTodos.findIndex(
            subTodo => subTodo.id === subTodoId
        );

        clonedTodos[todoIndex].subTodos.splice(subTodoIndex, 1);

        return clonedTodos;
    };

    const editTodoTitle = (todoId, newTitle) => prevState => {
        const clonedTodos = cloneObject(prevState);
        const todoIndex = clonedTodos.findIndex(todo => todo.id === todoId);

        clonedTodos[todoIndex].title = newTitle;

        return clonedTodos;
    };

    const editSubTodoTitle = (todoId, subTodoId, newTitle) => prevState => {
        const clonedTodos = cloneObject(prevState);
        const todoIndex = clonedTodos.findIndex(todo => todo.id === todoId);
        const subTodoIndex = clonedTodos[todoIndex].subTodos.findIndex(
            subTodo => subTodo.id === subTodoId
        );

        clonedTodos[todoIndex].subTodos[subTodoIndex].title = newTitle;

        return clonedTodos;
    };

    return Object.freeze({
        addTodo,
        addSubTodo,
        toggleTodo,
        toggleSubTodo,
        deleteTodo,
        deleteSubTodo,
        editTodoTitle,
        editSubTodoTitle
    });
};

export default todoActions();
