import React, { useState } from 'react';
import todoActions from './actions';
import tempData from './tempData';
import Context from './context';

const Provider = ({ children }) => {
    const [todos, setTodos] = useState(tempData);

    const addTodo = () => setTodos(todoActions.addTodo());
    const addSubTodo = todoId => setTodos(todoActions.addSubTodo(todoId));
    const toggleTodo = todoId => setTodos(todoActions.toggleTodo(todoId));
    const toggleSubTodo = (todoId, subTodoId) =>
        setTodos(todoActions.toggleSubTodo(todoId, subTodoId));
    const deleteTodo = todoId => setTodos(todoActions.deleteTodo(todoId));
    const deleteSubTodo = (todoId, subTodoId) =>
        setTodos(todoActions.deleteSubTodo(todoId, subTodoId));
    const editTodoTitle = (todoId, newTitle) =>
        setTodos(todoActions.editTodoTitle(todoId, newTitle));
    const editSubTodoTitle = (todoId, subTodoId, newTitle) =>
        setTodos(todoActions.editSubTodoTitle(todoId, subTodoId, newTitle));

    const actions = {
        addTodo,
        addSubTodo,
        toggleTodo,
        toggleSubTodo,
        deleteTodo,
        deleteSubTodo,
        editTodoTitle,
        editSubTodoTitle
    };

    return (
        <Context.Provider value={{ todos, actions }}>
            {children}
        </Context.Provider>
    );
};

export default Provider;
