import React, { useState, useContext } from 'react';
import { GlobalContext } from '../../context';
import { todoTypes } from '../../types';
import Checkbox from '../Checkbox';
import TodoCornerLogo from '../TodoCornerLogo';
import DeleteButton from '../DeleteButton';
import SubTodo from '../SubTodo';
import NewSubTodoButton from '../NewSubTodoButton';
import TodoTitle from '../TodoTitle';
import TodoInput from '../TodoInput';
import { sortByCompletionStatus } from '../../utils/helpers';
import './Todo.scss';

const Todo = ({ todo: { id, title, isComplete, subTodos } }) => {
    const [isOpen, setIsOpen] = useState(false);
    const [isEditMode, setIsEditMode] = useState(false);
    const {
        actions: { toggleTodo, deleteTodo, editTodoTitle, addSubTodo }
    } = useContext(GlobalContext);

    const shouldShowCornerLogo = !isOpen && !isComplete && subTodos.length > 0;
    const todoClassName = isComplete
        ? 'Todo-todo Todo-todo-isComplete'
        : 'Todo-todo';

    const setNewTitle = newTitle => {
        if (newTitle) {
            editTodoTitle(id, newTitle);
        }
        setIsEditMode(false);
    };

    const renderTitle = (
        <TodoTitle
            title={title}
            onClick={() => setIsOpen(!isOpen)}
            onLongPress={() => setIsEditMode(true)}
        />
    );

    const renderInput = (
        <TodoInput
            value={title}
            setNewTitle={newTitle => setNewTitle(newTitle)}
        />
    );

    const renderSubTodos = (
        <ul className="Todo-subTodos">
            {subTodos
                .sort((a, b) => sortByCompletionStatus(a, b))
                .map(subTodo => (
                    <SubTodo key={subTodo.id} subTodo={subTodo} todoId={id} />
                ))}
        </ul>
    );

    return (
        <li className="Todo">
            <div className={todoClassName}>
                <Checkbox checked={isComplete} onClick={() => toggleTodo(id)} />
                {isEditMode ? renderInput : renderTitle}
                {shouldShowCornerLogo && <TodoCornerLogo />}
                {isComplete && <DeleteButton onClick={() => deleteTodo(id)} />}
            </div>
            {isOpen && renderSubTodos}
            {isOpen && <NewSubTodoButton onClick={() => addSubTodo(id)} />}
        </li>
    );
};

Todo.propTypes = {
    todo: todoTypes.todoType.isRequired
};

export default Todo;
