import { number, string, bool, shape, arrayOf } from 'prop-types';

const idType = number;
const titleType = string;
const isCompleteType = bool;

const subTodoType = shape({
    id: idType.isRequired,
    title: titleType.isRequired,
    isComplete: isCompleteType.isRequired
});

const todoType = shape({
    id: idType.isRequired,
    title: titleType.isRequired,
    isComplete: isCompleteType.isRequired,
    subTodos: arrayOf(subTodoType).isRequired
});

const todoTypes = { todoType, subTodoType, idType, titleType, isCompleteType };

export default todoTypes;
