import React, { useState, useContext } from 'react';
import Checkbox from '../Checkbox';
import DeleteButton from '../DeleteButton';
import TodoTitle from '../TodoTitle';
import TodoInput from '../TodoInput';
import { GlobalContext } from '../../context';
import { todoTypes } from '../../types';
import './SubTodo.scss';

const SubTodo = ({ subTodo: { id, title, isComplete }, todoId }) => {
    const [isEditMode, setIsEditMode] = useState(false);
    const {
        actions: { toggleSubTodo, deleteSubTodo, editSubTodoTitle }
    } = useContext(GlobalContext);

    const setNewTitle = newTitle => {
        if (newTitle) {
            editSubTodoTitle(todoId, id, newTitle);
        }
        setIsEditMode(false);
    };

    const renderTitle = (
        <TodoTitle title={title} onLongPress={() => setIsEditMode(true)} />
    );

    const renderInput = (
        <TodoInput
            value={title}
            setNewTitle={newTitle => setNewTitle(newTitle)}
        />
    );

    return (
        <li className={isComplete ? 'SubTodo SubTodo-isComplete' : 'SubTodo'}>
            <Checkbox
                checked={isComplete}
                onClick={() => toggleSubTodo(todoId, id)}
            />
            {isEditMode ? renderInput : renderTitle}
            {isComplete && (
                <DeleteButton onClick={() => deleteSubTodo(todoId, id)} />
            )}
        </li>
    );
};

SubTodo.propTypes = {
    subTodo: todoTypes.subTodoType.isRequired,
    todoId: todoTypes.idType.isRequired
};

export default SubTodo;
