import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { todoTypes } from '../../types';
import './TodoTitle.scss';

const TodoTitle = ({ title, onClick, onLongPress }) => {
    const [clickTimer, setClickTimer] = useState(null);
    const [isLongPress, setIsLongPress] = useState(false);

    const startClickTimer = () => {
        const timer = setTimeout(() => {
            setIsLongPress(true);
            onLongPress();
        }, 2000);

        setClickTimer(timer);
    };

    const stopClickTimer = e => {
        e.preventDefault();
        if (!isLongPress) {
            onClick();
        }
        clearTimeout(clickTimer);
        setIsLongPress(false);
    };

    return (
        <h3
            className="TodoTitle"
            onMouseDown={startClickTimer}
            onMouseUp={e => stopClickTimer(e)}
            onTouchStart={startClickTimer}
            onTouchEnd={e => stopClickTimer(e)}
        >
            {title}
        </h3>
    );
};

TodoTitle.propTypes = {
    title: todoTypes.titleType.isRequired,
    onClick: PropTypes.func,
    onLongPress: PropTypes.func.isRequired
};

TodoTitle.defaultProps = {
    onClick: () => {}
};

export default TodoTitle;
